/* Minimal styles for GenerateInvoice form */
/* Dark realistic invoice styles */
.invoice-page { padding:28px 40px; color:#e6eef8; background: radial-gradient(1200px 400px at 10% 10%, rgba(96,165,250,0.06), transparent 10%), linear-gradient(180deg,#050506 0%, #000000 100%); min-height:70vh; }
.invoice-hero { display:flex; align-items:center; gap:24px; margin-bottom:20px; }
.hero-left h1 { margin:0; font-size:30px; color:#8be0ff; letter-spacing:0.2px; }
.hero-left .sub { color:#9fb0c7; margin-top:6px; font-size:14px }
.hero-right { margin-left:auto; }
.summary-card { background:linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01)); padding:16px 20px; border-radius:12px; box-shadow:0 18px 50px rgba(2,6,23,0.8); width:240px; border:1px solid rgba(255,255,255,0.03); }
.s-row { display:flex; justify-content:space-between; color:#cbd5e1; padding:8px 0; }
.s-row.total { font-size:18px; color:#ffffff; margin-top:6px; }

.invoice-container { display:flex; gap:24px; align-items:flex-start; }
.card { background: linear-gradient(180deg, rgba(255,255,255,0.01), rgba(255,255,255,0.006)); border-radius:12px; padding:20px; box-shadow: 0 24px 60px rgba(0,0,0,0.8); border:1px solid rgba(255,255,255,0.02); }
.invoice-form { flex:1; }
.preview { width:360px; }
.form-row { margin-bottom:14px; }
.form-row label { display:block; color:#9fb0c7; margin-bottom:8px; font-size:13px }
.invoice-form input, .invoice-form textarea { width:100%; padding:12px 14px; border-radius:10px; border:1px solid rgba(255,255,255,0.04); background:rgba(255,255,255,0.01); color:#e6eef8; font-size:14px }
.invoice-form textarea { min-height:90px; }
.lines { margin-top:14px; }
.lines-head { display:grid; grid-template-columns: 1fr 80px 120px 120px 80px; gap:12px; align-items:center; color:#9fb0c7; margin-bottom:10px; font-size:13px }
.line-row { display:grid; grid-template-columns: 1fr 80px 120px 120px 80px; gap:12px; align-items:center; margin-bottom:10px; }
.line-row .desc { width:100%; }
.line-row .qty, .line-row .price { width:100%; }
.line-total { padding:8px 10px; background:rgba(255,255,255,0.02); border-radius:6px; text-align:right; color:#e6eef8; font-weight:600 }
.remove-btn { background:transparent; border:0; color:#ff7a84; cursor:pointer; }
.form-actions { display:flex; gap:12px; justify-content:space-between; align-items:center; margin-top:16px; }
.btn-ghost { background:transparent; border:1px solid rgba(255,255,255,0.06); padding:10px 14px; border-radius:10px; cursor:pointer; color:#cbd5e1 }
.btn-primary { background:linear-gradient(90deg,#ffb86b,#ff6b81); color:#081018; padding:10px 18px; border-radius:10px; border:0; cursor:pointer; box-shadow:0 12px 30px rgba(255,107,129,0.08); font-weight:700 }
.preview-body { color:#cbd5e1; }
.preview { position:relative }
.invoice-paper { background: linear-gradient(180deg,#06070a 0%, #05060a 60%); border-radius:10px; padding:18px; border:1px solid rgba(255,255,255,0.04); box-shadow: 0 28px 80px rgba(2,6,23,0.85); position:relative; overflow:hidden; }
.invoice-paper h4 { margin:0 0 8px 0; color:#e6eef8; font-size:18px; }

/* subtle paper grain using pseudo element */
.invoice-paper:before { content: ''; position:absolute; inset:0; background-image: radial-gradient(rgba(255,255,255,0.01) 1px, transparent 1px); background-size:8px 8px; opacity:0.06; pointer-events:none; }

/* watermark */
.invoice-paper .watermark { position:absolute; left:50%; top:44%; transform:translate(-50%,-50%) rotate(-20deg); font-size:72px; color:rgba(255,255,255,0.04); font-weight:800; letter-spacing:6px; pointer-events:none; user-select:none; }

/* header: logo + company info */
.invoice-paper .header-left { display:flex; align-items:center; gap:12px }
.invoice-paper .logo { width:56px; height:56px; border-radius:8px; display:flex; align-items:center; justify-content:center; font-weight:800; color:#041027; background:linear-gradient(90deg,#ffd27a,#ff7b9b); box-shadow:0 8px 28px rgba(255,107,129,0.08); }
.invoice-paper .company { font-size:13px; color:#9fb0c7 }

/* colored totals and separator */
.invoice-paper .sep { height:1px; background:linear-gradient(90deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01)); margin:12px 0; }
.preview-total { margin-top:12px; font-size:16px; text-align:right; color:#ffffff }
.preview-total strong { background:linear-gradient(90deg,#ffd27a,#ff6b81); -webkit-background-clip:text; background-clip:text; color:transparent }

/* print/export button */
.btn-secondary { background:linear-gradient(90deg,#60a5fa,#7c3aed); color:white; padding:8px 12px; border-radius:10px; border:0; cursor:pointer; font-weight:700; box-shadow:0 10px 30px rgba(96,165,250,0.08); }
.btn-secondary:hover { transform:translateY(-2px); }
.invoice-table { width:100%; border-collapse:collapse; margin-top:12px; }
.invoice-table th, .invoice-table td { padding:8px 6px; text-align:left; border-bottom:1px dashed rgba(255,255,255,0.02); font-size:14px }
.invoice-table th { color:#9fb0c7; font-weight:600 }
.invoice-table td.amount { text-align:right; font-weight:600 }
.preview-total { margin-top:12px; font-size:16px; text-align:right; color:#ffffff }
.login-success { color: #065f46; background:#063b2b; padding:8px 10px; border-radius:6px; }
.login-error { color:#7f1d1d; background:#2b0b0b; padding:8px 10px; border-radius:6px; }

/* accent tag */
.tag-accent { display:inline-block; padding:6px 10px; border-radius:999px; background:linear-gradient(90deg,#7c3aed,#60a5fa); color:#041027; font-weight:700; box-shadow:0 8px 28px rgba(96,165,250,0.06) }

@media (max-width:900px) {
	.invoice-container { flex-direction:column; }
	.preview { width:100%; }
	.invoice-hero { flex-direction:column; align-items:flex-start; }
}

/* Modal for invoice listing */
.invoice-modal-overlay { position:fixed; inset:0; background:linear-gradient(180deg, rgba(2,6,23,0.7), rgba(2,6,23,0.9)); display:flex; align-items:center; justify-content:center; z-index:1200; }
.invoice-modal { background:linear-gradient(180deg,#05060a 0%, #0b0b0d 100%); border-radius:10px; padding:18px; width:880px; max-width:96%; box-shadow:0 30px 80px rgba(0,0,0,0.8); border:1px solid rgba(255,255,255,0.02); color:#e6eef8 }
.invoice-modal table td { font-size:14px }
.invoice-modal h3 { margin:0 }
