.login-wrapper {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f5f5;
  font-family: "Arial", sans-serif;
}

.login-box {
  width: 330px;
  /* soft gradient + subtle textured look instead of plain white */
  background: linear-gradient(180deg, rgba(255,255,255,0.98) 0%, rgba(245,247,250,0.92) 100%),
              radial-gradient(rgba(0,0,0,0.02) 1px, transparent 1px);
  background-size: auto, 10px 10px;
  padding: 35px 30px;
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(15, 20, 30, 0.18);
  border: 1px solid rgba(0,0,0,0.04);
}

.login-title {
  margin-bottom: 25px;
  text-align: center;
  color: #007bff;
  font-size: 22px;
}

.demo-btn {
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid rgba(0,0,0,0.06);
  background: #fff;
  cursor: pointer;
}

.login-error {
  color: #b00020;
  text-align: center;
  margin-top: 10px;
}

.login-success {
  color: #0b6623;
  text-align: center;
  margin-top: 10px;
}

.login-box input {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  font-size: 15px;
  border: 1px solid rgba(30, 40, 50, 0.08);
  border-radius: 8px;
  background: rgba(255,255,255,0.85);
}

.login-btn {
  width: 100%;
  padding: 12px;
  background: #007bff;
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
}

.forgot-link, .signup-link, .login-hint {
  text-align: center;
  margin-top: 10px;
}
