.signup-wrapper {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f5f5;
}

.signup-box {
  width: 350px;
  background: #fff;
  padding: 35px;
  border-radius: 10px;
  box-shadow: 0 4px 14px rgba(0,0,0,0.1);
}

.signup-title {
  text-align: center;
  color: #007bff;
  margin-bottom: 20px;
}

.signup-box input {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.signup-btn {
  width: 100%;
  padding: 12px;
  background: #007bff;
  border: none;
  color: white;
  border-radius: 6px;
}

.signup-hint {
  text-align: center;
  margin-top: 10px;
}
