/* Interactive user home styles */

.userhome-hero { position:relative; overflow:hidden; min-height:70vh; display:flex; align-items:center; padding:64px 48px; color:#e6eef8; background:#050507; }
.animated-bg { position:absolute; inset:0; pointer-events:none; }
.blob { position:absolute; filter:blur(36px); opacity:0.9; transform:translate3d(0,0,0) scale(1); transition: transform 0.14s linear, opacity 0.4s ease; will-change: transform; border-radius:50%; }
.blob.b1 { width:380px; height:380px; left:-8%; top:-6%; background:linear-gradient(135deg,#7c3aed 10%,#60a5fa 80%); mix-blend-mode:screen; }
.blob.b2 { width:320px; height:320px; right:-6%; top:18%; background:linear-gradient(135deg,#fb7185 10%,#f97316 80%); mix-blend-mode:screen; }
.blob.b3 { width:260px; height:260px; left:18%; bottom:-6%; background:linear-gradient(135deg,#34d399 10%,#60a5fa 85%); mix-blend-mode:screen; }

.userhome-hero { --mx:0; --my:0; }
.blob.b1 { transform: translate(calc(var(--mx) * 22px), calc(var(--my) * 22px)) scale(1.02); }
.blob.b2 { transform: translate(calc(var(--mx) * -28px), calc(var(--my) * 14px)) scale(1.02); }
.blob.b3 { transform: translate(calc(var(--mx) * 12px), calc(var(--my) * -24px)) scale(1.02); }

/* gentle pulsing */
@keyframes pulseScale { 0% { transform: scale(1); } 50% { transform: scale(1.06); } 100% { transform: scale(1); } }
.blob.b1 { animation: pulseScale 6s ease-in-out infinite; }
.blob.b2 { animation: pulseScale 7s ease-in-out 0.6s infinite; }
.blob.b3 { animation: pulseScale 5.5s ease-in-out 0.3s infinite; }

.userhome-content { position:relative; z-index:2; max-width:1200px; display:flex; gap:36px; align-items:center; opacity:0; transform:translateY(8px); transition: opacity 420ms ease, transform 420ms ease; }
.userhome-hero.entered .userhome-content { opacity:1; transform:none; }

.userhome-content h1 { font-size:56px; line-height:1.02; color:#7dd3fc; margin:0 0 8px 0; text-shadow: 0 6px 18px rgba(12,18,34,0.6); }
.userhome-content h1 span { color:#60a5fa; }
.userhome-content .lead { color:#b9d0e6; max-width:520px; margin:6px 0 0 0; font-size:16px; }

.feature-grid { display:flex; gap:18px; margin-top:18px; }
.feature { background: rgba(255,255,255,0.02); padding:18px; border-radius:14px; min-width:200px; box-shadow: 0 6px 24px rgba(2,6,23,0.6); transform: translateY(8px); opacity:0; transition: transform 360ms cubic-bezier(.2,.9,.2,1), opacity 360ms ease; }
.userhome-hero.entered .feature { opacity:1; transform:none; }
.feature:nth-child(1) { transition-delay: 160ms }
.feature:nth-child(2) { transition-delay: 260ms }
.feature:nth-child(3) { transition-delay: 360ms }
.feature h4 { margin:0 0 6px 0; color:#e6eef8; font-size:18px; }
.feature p { margin:0; color:#9fb0c7; font-size:14px; }
.feature:hover { transform: translateY(-6px) scale(1.02); box-shadow: 0 16px 48px rgba(2,6,23,0.7); }

.hero-illustration { margin-left:auto; opacity:0.98; transform: translateY(6px); transition: transform 420ms ease, opacity 420ms ease; }
.userhome-hero.entered .hero-illustration { transform:none; opacity:1; }

/* subtle particles via pseudo elements */
.userhome-hero::before,
.userhome-hero::after { content: ""; position:absolute; pointer-events:none; border-radius:50%; opacity:0.06; }
.userhome-hero::before { width:640px; height:640px; left:-10%; top:10%; background: radial-gradient(circle at 30% 30%, #60a5fa, transparent 40%); }
.userhome-hero::after { width:520px; height:520px; right:-6%; bottom:6%; background: radial-gradient(circle at 70% 70%, #fb7185, transparent 40%); }

@media (max-width:900px) {
  .userhome-content { flex-direction:column; text-align:left; padding:18px; }
  .hero-illustration { margin-left:0; margin-top:18px; }
  .userhome-content h1 { font-size:36px }
}

