/* ===== GLOBAL HARD RESET ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
  overflow-x: hidden !important;
  background: #000; /* prevents white flash */
}

/* Remove all unwanted spacing */
#root {
  width: 100%;
  overflow-x: hidden;
}

/* FIX: main content starts right below navbar */
main {
  padding-top: 78px !important; /* exact navbar height */
  margin: 0 !important;
}
