:root{
  --bg:#0b1220;
  --panel:#0f1724;
  --muted:rgba(255,255,255,0.65);
  --neon1:#4ee0ff;
  --neon2:#8a6bff;
  --accent:#4da3ff;
}

.dash-root{
  min-height: calc(100vh - 72px);
  padding: 28px;
  background: linear-gradient(180deg, rgba(8,10,14,0.7) 0%, rgba(5,7,11,0.5) 100%), var(--bg);
  color: white;
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
}

/* Header */
.dash-header{
  display:flex;
  justify-content:space-between;
  align-items:center;
  margin-bottom:18px;
}
.dash-title{
  margin:0;
  font-size:22px;
  letter-spacing:0.3px;
  color:var(--neon1);
}
.dash-sub{ margin:0; color:var(--muted); font-size:13px; }

/* CTA */
.btn-primary{
  background: linear-gradient(90deg,var(--neon2),var(--accent));
  color:#061123;
  border:none;
  padding:8px 14px;
  border-radius:8px;
  font-weight:700;
  cursor:pointer;
}
.btn-ghost{
  background:transparent;
  color:var(--muted);
  border:1px solid rgba(255,255,255,0.06);
  padding:6px 10px;
  border-radius:8px;
  margin-right:10px;
}

/* cards grid */
.cards-grid{
  display:grid;
  grid-template-columns: repeat(4,1fr);
  gap:16px;
  margin-bottom:18px;
}
.card{
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border: 1px solid rgba(255,255,255,0.04);
  padding:18px;
  border-radius:12px;
  box-shadow: 0 8px 30px rgba(2,8,23,0.6);
}
.card .card-top{ display:flex; justify-content:space-between; align-items:center; margin-bottom:8px; }
.card-label{ color:var(--muted); font-size:13px; }
.card-value{ font-size:20px; font-weight:800; color:var(--neon1); }

/* alert card */
.card.alert{ border-left:4px solid #ff6b6b; }

/* charts row */
.charts-row{
  display:grid;
  grid-template-columns: 1.2fr .8fr;
  gap:16px;
  margin-bottom:18px;
}
.chart-card{
  background: var(--panel);
  border:1px solid rgba(255,255,255,0.03);
  padding:12px;
  border-radius:12px;
}

/* chart title */
.chart-title{
  color:var(--muted);
  margin:0 0 6px 6px;
  font-size:14px;
}

/* table card */
.table-card{
  background: var(--panel);
  border:1px solid rgba(255,255,255,0.03);
  padding:12px;
  border-radius:12px;
}

/* table header row */
.table-header{
  display:flex;
  justify-content:space-between;
  align-items:center;
  margin-bottom:12px;
}
.table-header h3{ margin:0; color:var(--neon2); }
.table-actions{ display:flex; gap:8px; align-items:center; }
.search-input{
  padding:8px 12px;
  border-radius:8px;
  background:#09111a;
  border:1px solid rgba(255,255,255,0.03);
  color:var(--muted);
}

/* table */
.neon-table{
  width:100%;
  border-collapse:collapse;
  color:var(--muted);
}
.neon-table th, .neon-table td{
  text-align:left;
  padding:12px 10px;
  border-bottom:1px solid rgba(255,255,255,0.03);
  font-size:14px;
}
.neon-table thead th{ color:rgba(255,255,255,0.8); font-weight:700; font-size:13px; }
.low-stock{ color:#ffb86b; font-weight:700; }

/* action buttons */
.link-btn{
  background:transparent;
  color:var(--neon1);
  border:none;
  margin-right:8px;
  cursor:pointer;
  font-weight:700;
}
.danger-btn{
  background:#ff4d4f;
  color:white;
  border:none;
  padding:6px 10px;
  border-radius:6px;
  cursor:pointer;
}

/* responsive */
@media (max-width: 980px){
  .cards-grid{ grid-template-columns: repeat(2,1fr); }
  .charts-row{ grid-template-columns: 1fr; }
}
@media (max-width: 520px){
  .cards-grid{ grid-template-columns: 1fr; }
  .dash-title{ font-size:18px; }
  .hero { padding:12px; }
}
